details {
    summary {
        cursor: pointer;
        user-select: none;
        
        h2,
        h3,
        h4,
        h5 {
            display: inline-block;
        }
    }
}

/* Responsive layout - makes a one column layout instead of a two-column layout */
@media (max-width: 800px) {
    .movie-content {
        flex-direction: column !important;
    }
    .comments {
        padding-top: 2em;
        padding-left: 0 !important;
    }
}

.movie-content {
    display: flex;
    flex-direction: row;
    // justify-content: flex-start;
    // align-items: flex-start;

    .cover {
        flex: 1;
    }
    .comments {
        flex: 1;
        padding-left: 2em;
    }
}

.list-box {
    width: 12em;
    display: inline-block;
    margin: 1em;
}

form {
    input[type="text"], input[type="email"] {
        padding: 0.5em;
        width: 15em;
    }
    input[type="submit"] {
        padding: 0.5em;
        width: 8em;
        font-size: 1em;
    }
}